import Grid from "@mui/material/Grid";
import styled from "styled-components";
import Avatar from "./components/PhotoComponent";
import {useEffect} from "react"

const HomeContainer = styled.div`
  width: 99%;
  height: 100%;
`;

const FirstSectionContainer = styled(Grid)`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 100vh;
`;

const SecondSectionContainer = styled(Grid)`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: 10px;
  height: 100vh;
  border: 1px solid black;
`;

const ThirdSectionContainer = styled(Grid)`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  padding: 10px;
  height: 100vh;
  border: 1px solid black;
`;

const Home = () => {

    useEffect(() => {
        document.title = "Mateusz Pawelak | Home";
    }, []);

    return (
        <HomeContainer>
            <FirstSectionContainer item xs={12}>
                <Avatar/>
            </FirstSectionContainer>
            <SecondSectionContainer item xs={12}>
                <h1>Who i'm</h1>
            </SecondSectionContainer>
            <ThirdSectionContainer item xs={12}>
                <h1>My skills</h1>
            </ThirdSectionContainer>
        </HomeContainer>
    )
}

export default Home;