import {useEffect} from "react";
import EmailIcon from '@mui/icons-material/Email';
import InstagramIcon from '@mui/icons-material/Instagram';
import PhotoCameraBackIcon from '@mui/icons-material/PhotoCameraBack';
import Grid from "@mui/material/Grid";
import IconBox from "./components/IconBox";

const Contact = () => {

    useEffect(() => {
        document.title = "Mateusz Pawelak | Contact";
    }, []);

    return (
        <Grid container width={'100%'} height={'10%'} style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
            <IconBox icon={<EmailIcon/>} title={"Mail to me"}/>
            <IconBox icon={<InstagramIcon/>} title={"See my instagram"}/>
            <IconBox icon={<PhotoCameraBackIcon/>} title={"Find me on devianart"}/>
            <h1>Jeba to gwno nie wychodzi mi</h1>
        </Grid>
    )
}

export default Contact;