import {Box, Fade, ImageList, ImageListItem, Modal} from "@mui/material";
import styled from "styled-components";
import {useState} from "react";

const ArtListSet = styled(ImageList)`
  flex-wrap: nowrap;
  margin: 0;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }
`;

const BoxStyle = {
    width: 'auto',
    height: '90%',
    display: 'flex',
    justifyContent: 'center',
    background: 'transparent',
    backdropFilter: 'blur(5px)',
    padding: '20px 20px 30px 20px',
    borderRadius: '30px',
}

const TitleBar = styled.div`
  position: absolute;
  bottom: 7px;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  z-index: 10;
  letter-spacing: 3px;
  color: white;
  -webkit-text-stroke: 0.1px black;
`;

function srcset(image, size, rows = 1, cols = 1) {
    return {
        src: `${image}?w=${size * cols}&h=${size * rows}&fit=crop&auto=format`,
        srcSet: `${image}?w=${size * cols}&h=${
            size * rows
        }&fit=crop&auto=format&dpr=2 2x`,
    };
}

const ArtsList = (props) => {
    const [open, setOpen] = useState(false);
    const [src, setSrc] = useState("");
    const [title, setTitle] = useState("");

    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    return (
        <ArtListSet
            sx={{width: '100%', height: '100%'}}
            variant="quilted"
            cols={6}
            gap={20}
        >
            {props.artList.map((item) => (
                <ImageListItem
                    key={item.src}
                    cols={item.cols || 1}
                    rows={item.rows || 1}
                >
                    <img
                        {...srcset(item.src, 500, item.rows, item.cols)}
                        alt={item.title}
                        loading="lazy"
                        style={{cursor: "pointer"}}
                        onClick={() => {
                            setSrc(item.src);
                            setTitle(item.title);
                            handleOpen();
                        }}
                    />
                </ImageListItem>
            ))}
            <Modal
                open={open}
                onClose={handleClose}
                closeAfterTransition
                style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    border: 'none',
                    background: 'transparent'
                }}
            >
                <Fade in={open}>
                    <Box sx={BoxStyle}>
                        <TitleBar>{title}</TitleBar>
                        <img src={src} alt={title}
                             style={{objectFit: 'contain', width: '100%', height: '100%', pointerEvents: 'none'}}/>
                    </Box>
                </Fade>
            </Modal>
        </ArtListSet>
    )
}

export default ArtsList;